const StyledMenuIcon = styled(Database)`
  height: 24px;
  width: 24px;
  > * {
    stroke: ${({ theme }) => theme.text2};
  }
`

const StyledMenuButton = styled.button`
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  margin-left: 1rem;
  padding: 0px 10px;
  height: 36px;
  border-radius: 0.5rem;
  
  :hover,
  :focus {
    cursor: pointer;
    outline: none;
    color: white;
    background-color: rgba(0,64,0,0.5);
  }

  svg {
    margin-top: 2px;
  }
`

const StyledMenu = styled.div`
  margin-left: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const MenuFlyout = styled.span`
  min-width: 8.125rem;
  background-color: ${({ theme }) => theme.bg3};
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 12px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 4rem;
  left: 0rem;
  z-index: 100;
`

export const MenuItem = styled(ExternalLink)`
  flex: 1;
  padding: 0.5rem 0.5rem;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
`

export default function MenuInfo() {
  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.MENU_INFO)
  const toggle = useToggleModal(ApplicationModal.MENU_INFO)
  useOnClickOutside(node, open ? toggle : undefined)

  return (
    <StyledMenu ref={node as any}>
      <StyledMenuButton onClick={toggle}>
        <AutoRow>
          <StyledMenuIcon />
          <Text fontSize={20} style={{padding:'0px 6px'}}> Info </Text>
        </AutoRow>
      </StyledMenuButton>

      {open && (
        <MenuFlyout>
          <MenuItem id="link" href="https://www.arkreen.com">
            <Home size={14} />
            Arkreen
          </MenuItem>
          <MenuItem id="link" href="https://explorer.arkreen.com/">
            <List size={14} />
            Explorer
          </MenuItem>
          <MenuItem id="link" href="https://docs.arkreen.com">
            <BookOpen size={14} />
            Docs
          </MenuItem>
          <MenuItem id="link" href="https://github.com/arkreen">
            <GitHub size={14} />
            Github
          </MenuItem>
          <MenuItem id="link" href="https://twitter.com/arkreen_network">
            <MessageCircle size={14} />
            Twitter
          </MenuItem>          
        </MenuFlyout>
      )}
    </StyledMenu>
  )
}
